/* Preloder */
#preloder {
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	z-index: 999999;
	background: #000;
}
.loader {
	width: 40px;
	height: 40px;
	position: absolute;
	top: 50%;
	left: 50%;
	margin-top: -13px;
	margin-left: -13px;
	border-radius: 60px;
	animation: loader 0.8s linear infinite;
	-webkit-animation: loader 0.8s linear infinite;
}
@keyframes loader {
0% {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
		border: 4px solid #f44336;
		border-left-color: transparent;
}
50% {
		-webkit-transform: rotate(180deg);
		transform: rotate(180deg);
		border: 4px solid #673ab7;
		border-left-color: transparent;
}
100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
		border: 4px solid #f44336;
		border-left-color: transparent;
}
}
@-webkit-keyframes loader {
0% {
		-webkit-transform: rotate(0deg);
		border: 4px solid #f44336;
		border-left-color: transparent;
}
50% {
		-webkit-transform: rotate(180deg);
		border: 4px solid #673ab7;
		border-left-color: transparent;
}
100% {
		-webkit-transform: rotate(360deg);
		border: 4px solid #f44336;
		border-left-color: transparent;
}
}

/*---------------------
  Header
-----------------------*/
.header {
	background: rgb(48, 47, 47);
	width: 100%;
	z-index: 9;
}
.header.header--normal {
	position: relative;
	background: #290849;
}
.header__logo {
	padding: 20px 0 25px;
}
.header__logo a {
	display: inline-block;
}
.header__nav {
	text-align: right;
}
.header__menu {
	padding: 31px 0 27px;
	display: inline-block;
	margin-right: 45px;
}
.header__menu ul li {
	list-style: none;
	display: inline-block;
	margin-right: 45px;
	position: relative;
}
.header__menu ul li.active a:after {
	opacity: 1;
}
.header__menu ul li:hover a:after {
	opacity: 1;
}
.header__menu ul li:hover .dropdown {
	top: 34px;
	opacity: 1;
	visibility: visible;
}
.header__menu ul li:last-child {
	margin-right: 0;
}
.header__menu ul li .dropdown {
	position: absolute;
	left: 0;
	top: 56px;
	width: 150px;
	background: #111111;
	text-align: left;
	padding: 2px 0;
	z-index: 9;
	opacity: 0;
	visibility: hidden;
	-webkit-transition: all, 0.3s;
	transition: all, 0.3s;
}
.header__menu ul li .dropdown li {
	display: block;
	margin-right: 0;
}
.header__menu ul li .dropdown li a {
	font-size: 14px;
	color: #ffffff;
	font-weight: 400;
	padding: 8px 20px;
	text-transform: capitalize;
}
.header__menu ul li .dropdown li a:after {
	display: none;
}
.header__menu ul li a {
	font-size: 15px;
	text-transform: uppercase;
	color: #ffffff;
	display: block;
	padding: 6px 0;
	position: relative;
}
.header__menu ul li a:after {
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 2px;
	background: #ffffff;
	content: "";
	-webkit-transition: all, 0.5s;
	transition: all, 0.5s;
	opacity: 0;
}
.header__right__social {
	display: inline-block;
}
.header__right__social a {
	display: inline-block;
	font-size: 16px;
	color: #ffffff;
	margin-right: 16px;
}
.header__right__social a:last-child {
	margin-right: 0;
}
.slicknav_menu {
	display: none;
}

